<template>
  <div id="formulario-persona">
    <form v-on:submit.prevent="envia">
      <div class="container">
        <div class="row">
          <div class="col-md-4">
            <div class="form-group">
              <label>Nombre</label>
              <input v-model="persona.nombre" type="text" class="form-control" v-bind:class="{ 'is-invalid': procesando && nombreInvalido }" v-on:focus="resetEstado"/>
            </div>
          </div>
          <div class="col-md-4">
            <div class="form-group">
              <label>Apellido</label>
              <input v-model="persona.apellido" type="text" class="form-control" v-bind:class="{ 'is-invalid': procesando && apellidoInvalido }" v-on:focus="resetEstado"/>
            </div>
          </div>
          <div class="col-md-4">
            <div class="form-group">
              <label>Email</label>
              <input v-model="persona.email" type="email" class="form-control" v-bind:class="{ 'is-invalid': procesando && emailInvalido }" v-on:focus="resetEstado"/>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col-md-4">
            <div class="form-group">
              <button class="btn btn-primary">Añadir persona</button>
            </div>
          </div>
          <div v-if="error && procesando" class="alert alert-danger" role="alert">
            Debes rellenar todos los campos!
          </div>
          <div v-if="correcto" class="alert alert-success" role="alert">
            La persona ha sido agregada correctamente!
          </div>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
  export default {
    name: 'formulario-persona',
    data() {
      return {
        procesando: false,
        correcto: false,
        error: false,
        persona: {
          nombre: '',
          email: '',
          apellido: '',
        },
      }
    },
    methods:{
      envia(){
          this.procesando = true;
          this.resetEstado();
          
          // Comprobamos la presencia de errores
          if (this.nombreInvalido || this.apellidoInvalido || this.emailInvalido) {
            this.error = true;
            return;
          }
          
          this.$emit('afegeix-persona', this.persona);

          this.error = false;
          this.correcto = true;
          this.procesando = false;

          // Restablecemos el valor de la variables
          this.persona= {
            nombre: '',
            apellido: '',
            email: '',
          }
      },
      resetEstado() {
        this.correcto = false;
        this.error = false;
      }
    },
    computed: {
      nombreInvalido() {
        return this.persona.nombre.length < 1;
      },
      apellidoInvalido() {
        return this.persona.apellido.length < 1;
      },
      emailInvalido() {
        return this.persona.email.length < 1;
      },
    }
  }
</script>

<style scoped>
  form {
    margin-bottom: 2rem;
  }
</style>